import React, { Component } from 'react'
export class Confirm extends Component {
    continue = e => {
        e.preventDefault();
        this.props.nextStep();
    }
  render() {
      const { values: {firstName, lastName, email, 
     occupation, city, bio} } = this.props;
     const formStyle = {
         width: '40%',
         margin: '0 auto',
        marginTop:'5%',
        textAlign: 'center'
      }
      const btnStyle = {
        color: 'black',
        marginLeft: '20px'
    }

 
    return (
  <div>
  
      <nav>
    <div className="nav-wrapper">
      <span  className="brand-logo center">Confirm User Data</span>
    </div>
  </nav>
  <form style={formStyle}>
  <ul class="collection">
    <p><strong>First Name:</strong></p>
      <li class="collection-item">{firstName}</li>
      <p><strong>Last Name:</strong></p>
      <li class="collection-item">{lastName}</li>
      <p><strong>Email:</strong></p>
      <li class="collection-item">{email}</li>
      <p><strong>Occupation:</strong></p>
      <li class="collection-item">{occupation}</li>
      <p><strong>City:</strong></p>
      <li class="collection-item">{city}</li>    
      <p><strong>Bio:</strong></p>
      <li class="collection-item">{bio}</li>  
      
      </ul>

  <button 
    onClick={this.continue}
    className="btn waves-effect waves-light" 
    type="submit" name="action">Confirm</button>
    <button
    onClick={this.back}
    style={btnStyle}
    className="btn waves-effect waves-light grey lighten-5" 
    type="submit" name="action">Go back</button>
  </form>
        
  </div>
    )
  }
}

export default Confirm
