import React, { Component } from 'react'
export class FormUserDetails extends Component {
    continue = e => {
        e.preventDefault();
        this.props.nextStep();
    }
  render() {
      const { values, handleChange } = this.props;
      const formStyle = {
        marginLeft: "35%",
        marginTop:'5%'
      }
 
    return (
  <div>
  
      <nav>
    <div className="nav-wrapper">
      <span  className="brand-logo center">Enter User Details</span>
    </div>
  </nav>
  <form style={formStyle}>
  <div className="row">
    <div className="input-field col s6">
      <input onChange={handleChange('firstName')} 
      defaultValue={values.firstName}
      id="first_name2" type="text" className="validate" />
      <label className="active" for="first_name2">First Name</label>
    </div>
  </div>
  <div className="row">
    <div className="input-field col s6">
      <input 
      onChange={handleChange('lastName')} 
      defaultValue={values.lastName}
      id="last_name2" 
      type="text" className="validate" />
      <label className="active" for="last_name2">Last Name</label>
    </div>
  </div>
  <div className="row">
    <div className="input-field col s6">
      <input 
      onChange={handleChange('email')} 
      defaultValue={values.email}
       id="email" 
      type="text" className="validate" />
      <label className="active" for="email">Email</label>
    </div> 
  </div>
  <button 
    onClick={this.continue}
    className="btn waves-effect waves-light" 
    type="submit" name="action">Continue</button>
  </form>
        
  </div>
    )
  }
}

export default FormUserDetails
