import React, { Component } from 'react'
export class FormPersonalDetails extends Component {
    continue = e => {
        e.preventDefault();
        this.props.nextStep();
    }
    back = e => {
        e.preventDefault();
        this.props.prevStep();
    }
  render() {
      const { values, handleChange } = this.props;
      const formStyle = {
        marginLeft: "35%",
        marginTop:'5%'
      }
      const btnStyle = {
          color: 'black',
          marginLeft: '20px'
      }
 
    return (
  <div>
  
      <nav>
    <div className="nav-wrapper">
      <span  className="brand-logo center">Enter Personal Details</span>
    </div>
  </nav>
  <form style={formStyle}>
  <div className="row">
    <div className="input-field col s6">
      <input onChange={handleChange('occupation')} 
      defaultValue={values.occupation}
      id="occupation" type="text" className="validate" />
      <label className="active" for="occupation">Occupation</label>
    </div>
  </div>
  <div className="row">
    <div className="input-field col s6">
      <input 
      onChange={handleChange('city')} 
      defaultValue={values.city}
      id="city" 
      type="text" className="validate" />
      <label className="active" for="city">City</label>
    </div>
  </div>
  <div className="row">
    <div className="input-field col s6">
      <input 
       onChange={handleChange('bio')} 
      defaultValue={values.bio}
       id="bio" 
      type="text" className="validate" />
      <label className="active" for="bio">Bio</label>
    </div> 
  </div>
  <button 
    onClick={this.continue}
    className="btn waves-effect waves-light" 
    type="submit" name="action">Continue</button>
     <button
    onClick={this.back}
    style={btnStyle}
    className="btn waves-effect waves-light grey lighten-5" 
    type="submit" name="action">Go back</button>
  </form>
        
  </div>
    )
  }
}

export default FormPersonalDetails
